import "./App.css";
import { Form, Container } from "semantic-ui-react";
import { useState } from "react";
import "./main.css";

function App() {
  const [submited, setSubmited] = useState(false);
  function handleSubmit(e) {
    e.preventDefault();

    const url = "https://fabryrandom.herokuapp.com/";
    const data = new FormData();
    data.append("name", e.target.name.value.trim());
    fetch(url, {
      method: "post",
      body: data,
    });
    setSubmited(true);
  }
  return submited ? (
    <div className="loginBlock">
      <Container textAlign="center" text className="loginContainter">
        <h1>You have entered Good Luck!</h1>
      </Container>
    </div>
  ) : (
    <div className="App">
      <div className="loginBlock">
        <Container textAlign="center" text className="loginContainter">
          <Form onSubmit={handleSubmit}>
            <Form.Input
              required
              size="huge"
              focus
              icon="user"
              type="text"
              name="name"
              placeholder="Username"
            ></Form.Input>

            <Form.Button type="submit" size="huge" className="mt-2" primary>
              Submit
            </Form.Button>
          </Form>
        </Container>
      </div>
    </div>
  );
}

export default App;
